// medium blob near name/intro
@keyframes move1 {
  0% {
    transform: scale(1);
  }
  38% {
    transform: scale(0.7, 1.3) translate(10vw, -5vh) rotate(-30deg);
  }
  40% {
    transform: scale(0.7, 1.3) translate(10vw, -5vh) rotate(-30deg);
  }
  78% {
    transform: scale(1.2, 0.8) translate(-5vw, 10vh) rotate(30deg);
  }
  80% {
    transform: scale(1.2, 0.8) translate(-5vw, 10vh) rotate(30deg);
  }
  100% {
    transform: scale(1);
  }
}

